
.transition{transition:.3s all ease-in-out
}
.clr:before,#about section:nth-child(2) .container:before,#about section:nth-child(3) .container:before,.clr:after,#about section:nth-child(2) .container:after,#about section:nth-child(3) .container:after{content:'';clear:both;display:block
}
#about{background-color:#161616;color:#fff
}
#about section:first-child{padding:4em 0
}
#about section:first-child .title{border-bottom:1px solid #76b52a;padding:6em 0
}
#about section:first-child .title img{margin:0
}
#about section:nth-child(2){background-color:white;padding:2em 0
}
#about section:nth-child(2) .inner{transition:.3s all ease;background-image:url("../assets/images/components/about-bg-1.png");background-size:auto 100%;background-position:left center;background-repeat:no-repeat;color:#000;padding:8em 0
}
@media (min-width: 768px){
#about section:nth-child(2) .inner{padding:12em 0
}
}
@media (min-width: 1024px){
#about section:nth-child(2) .inner .content{max-width:960px;padding:0 1rem;margin:0 auto;box-sizing:border-box
}
}
@media (min-width: 1200px){
#about section:nth-child(2) .inner{background-position:-560px center
}
#about section:nth-child(2) .inner .content{max-width:none;width:57.62712%;float:left;margin-right:1.69492%;margin-left:6%;padding:0
}
}
@media (min-width: 1300px){
#about section:nth-child(2) .inner{background-position:-440px center
}
}
@media (min-width: 1680px){
#about section:nth-child(2) .inner{background-position:right center
}
#about section:nth-child(2) .inner .content{margin-left:0
}
}
#about section:nth-child(2) .container{position:relative
}
#about section:nth-child(2) .title{background-color:#fff;color:#76b52a;padding:1em 4em;position:absolute;display:inline-block;vertical-align:middle;margin:0 1em;top:-12em;left:0;box-shadow:0 0 3px rgba(0,0,0,0.33);max-width:340px
}
@media (min-width: 768px){
#about section:nth-child(2) .title{top:-18em
}
}
#about section:nth-child(3){background-image:url("../assets/images/components/about-bg-2.png");background-size:auto 90%;background-repeat:no-repeat;background-position:left top
}
#about section:nth-child(3) .container{padding-bottom:0
}
#about section:nth-child(3) .container .block:first-child h1{line-height:1
}
@media (min-width: 1024px){
#about section:nth-child(3) .block:first-child{max-width:960px;padding:0 1rem;margin:0 auto;box-sizing:border-box
}
}
@media (min-width: 1280px){
#about section:nth-child(3){background-position:right top;padding-top:6em
}
#about section:nth-child(3) .block{width:40%;display:table-cell;vertical-align:middle
}
#about section:nth-child(3) .block:first-child{max-width:none;padding-left:3em;padding-bottom:4em;margin:0
}
#about section:nth-child(3) .block:last-child{width:60%;vertical-align:bottom
}
}
@media (min-width: 1680px){
#about section:nth-child(3) .block:first-child{padding-left:0
}
}
#about section:last-child{padding:3em 0;background-color:#76b52a;background-image:url("../assets/images/components/about-bg-3.png");background-position:center;background-repeat:no-repeat
}
#about section:last-child .slider-wrapper{height:40vh;margin:2em 0
}
@media (min-width: 1024px){
#about section:last-child .slider-wrapper{height:50vh
}
}
@media (min-width: 1680px){
#about section:last-child .slider-wrapper{height:60vh
}
}
#about section:last-child #slider{margin:2em 0;height:100%
}
#about section:last-child #slider .item{height:100%;position:relative;background-image:url("../assets/images/components/about-csr-slide-1.png");background-position:center;background-size:cover
}
#about section:last-child #slider .slick-list,#about section:last-child #slider .slick-track{height:100%
}
#about section:last-child #slider .slick-dots li{width:40px;height:6px
}
#about section:last-child #slider .slick-dots li button{width:40px;height:6px;padding:0
}
#about section:last-child #slider .slick-dots li button:before{content:'';width:40px;height:6px;color:#fff;border:1px solid #fff;background-color:transparent;opacity:1
}
#about section:last-child #slider .slick-dots li.slick-active button:before{background-color:#fff
}
#about section:last-child #slider img{width:100%
}
#about section:last-child #slider .overlay{position:absolute;bottom:0;background-color:rgba(0,0,0,0.66);width:100%
}



