
.transition{transition:.3s all ease-in-out
}
.clr:before,.clr:after{content:'';clear:both;display:block
}
#inquiry{background-color:#161616;background-image:url("../assets/images/components/inquiry-bg-1.png");background-position:center top;background-repeat:no-repeat;color:#fff
}
.inquiry-wrapper .title{border-bottom:1px solid #76b52a
}
.inquiry-wrapper .title h1{font-size:3.6em;line-height:1
}
.inquiry-wrapper .title h1 span{display:block
}
.inquiry-list{overflow-x:auto
}
.inquiry-list .btn.rounded.green{padding:.3em 2em
}
table{width:100%;text-align:center
}
table th,table td{padding:.8em
}
table thead tr{color:#76b52a
}
table thead tr th{background-color:rgba(255,255,255,0.2)
}
table thead tr th:first-child{border-top-left-radius:2em;border-bottom-left-radius:2em
}
table thead tr th:last-child{border-top-right-radius:2em;border-bottom-right-radius:2em
}
table tbody td{border-bottom:1px solid #474747
}



