
.transition{transition:.3s all ease-in-out
}
.clr:before,.clr:after{content:'';clear:both;display:block
}
#login{background-color:rgba(71,71,71,0.9);position:fixed;top:0;right:0;left:0;z-index:9999;box-shadow:0 0 6px rgba(0,0,0,0.66)
}
#login h4{color:#fff
}
#login .call-action{padding-bottom:0
}
#login .call-action a{cursor:pointer
}
.login-wrapper>.container>.row>.block{margin-bottom:2em
}
.login-wrapper>.container>.row>.block:last-child{border-top:2px solid #fff
}
@media (min-width: 768px){
.login-wrapper>.container>.row>.block{box-sizing:border-box;width:50%;float:left;padding-left:2.08333%;padding-right:2.08333%;margin-bottom:0
}
.login-wrapper>.container>.row>.block:last-child{border-top:0;border-left:2px solid #fff;float:right;margin-right:0
}
}



