
.transition{transition:.3s all ease-in-out
}
.clr:before,.navigation .menu:before,.clr:after,.navigation .menu:after{content:'';clear:both;display:block
}
.navigation{background-color:#161616;height:80px;line-height:80px;text-align:center;position:relative;z-index:99
}
.navigation .restrict-xl{padding-top:0;padding-bottom:0
}
.navigation a{display:block;color:#fff
}
.navigation a.hover,.navigation a.active,.navigation a:active{color:#76b52a
}
.navigation a img{display:inline
}
.navigation .menu{margin:0;padding:0
}
.navigation .menu li{display:none
}
.navigation .menu li:nth-child(5){display:inline-block;vertical-align:middle;position:relative;bottom:-5px
}
#mobile-trigger{position:absolute;right:1em;top:0;padding:0 1em
}
#mobile-trigger .fa-2x{line-height:80px
}
.off-canvas{width:100%;max-width:300px;position:fixed;left:0;top:0;bottom:0;transform:translateX(-100%)
}
nav.off-canvas-navigation{background-color:#000;height:100%
}
nav.off-canvas-navigation .menu{margin:0;padding:0;box-sizing:border;list-style-type:none;color:#fff
}
nav.off-canvas-navigation .menu li{border-bottom:1px solid #161616
}
nav.off-canvas-navigation .menu li:nth-child(5){display:none
}
nav.off-canvas-navigation a{display:block;color:#fff;padding:1em 2em;transition:.3s all ease-in
}
nav.off-canvas-navigation a:hover,nav.off-canvas-navigation a:active,nav.off-canvas-navigation a.active{background-color:#76b52a;color:#000
}
#wrapper,.off-canvas{transition:.3s transform ease-in-out
}
#application.off_canvas_on .off-canvas{transform:translateX(0)
}
#application.off_canvas_on #wrapper{transform:translateX(300px)
}
@media (min-width: 1280px){
.navigation .menu li{width:11.11111%;float:left;display:inline-block;vertical-align:middle
}
.navigation .menu li:nth-child(9n+1){margin-left:0;margin-right:-100%;clear:both;margin-left:0
}
.navigation .menu li:nth-child(9n+2){margin-left:11.11111%;margin-right:-100%;clear:none
}
.navigation .menu li:nth-child(9n+3){margin-left:22.22222%;margin-right:-100%;clear:none
}
.navigation .menu li:nth-child(9n+4){margin-left:33.33333%;margin-right:-100%;clear:none
}
.navigation .menu li:nth-child(9n+5){margin-left:44.44444%;margin-right:-100%;clear:none
}
.navigation .menu li:nth-child(9n+6){margin-left:55.55556%;margin-right:-100%;clear:none
}
.navigation .menu li:nth-child(9n+7){margin-left:66.66667%;margin-right:-100%;clear:none
}
.navigation .menu li:nth-child(9n+8){margin-left:77.77778%;margin-right:-100%;clear:none
}
.navigation .menu li:nth-child(9n+9){margin-left:88.88889%;margin-right:-100%;clear:none
}
#mobile-trigger{display:none
}
}



