
.transition{transition:.3s all ease-in-out
}
.clr:before,#errorshooting .error-list ul:before,.clr:after,#errorshooting .error-list ul:after{content:'';clear:both;display:block
}
#errorshooting{background-color:#474747;color:#fff;padding:2em 0;box-sizing:border-box;background-image:url("../assets/images/components/error-bg.png");background-repeat:no-repeat;background-size:cover;background-position:left bottom;transition:.3s all ease;min-height:100vh
}
@media (min-width: 1280px){
#errorshooting{background-size:auto 90%;background-position:right top
}
}
@media (min-width: 1680px){
#errorshooting{background-position:right top
}
}
