}
#errorshooting .machine-type{background-color:rgba(0,0,0,0.8);border-radius:0 0 6px 6px;width:90%;margin:auto;box-sizing:border-box;padding:1em 0;position:absolute;top:56px;left:0;right:0;z-index:99;text-align:left
}
#errorshooting .machine-type h5{margin:0;padding:0 1em;color:#76b52a
}
#errorshooting .machine-type ul{padding:0 0 0 0;margin:1em 0;list-style-type:none
}
#errorshooting .machine-type ul a{font-size:1.1em;padding:.3em 0 .3em 3em;display:block;cursor:pointer
}
#errorshooting .machine-type ul a:hover{background-color:#161616
}
#errorshooting .error-list{position:relative
}
#errorshooting .error-list:before{content:'';border-bottom:1px solid #fff;width:100%;position:absolute;top:124px;left:0;box-sizing:border-box
}
@media (min-width: 1900px){
#errorshooting .error-list:before{top:80px
}
}
#errorshooting .error-list ul{padding:0 0 0 0;margin:0;list-style-type:none;position:relative
}
#errorshooting .error-list ul li{margin-top:4em;padding:2em 2em 0 2em;border:1px solid rgba(255,255,255,0.7);box-sizing:border-box
}
@media (min-width: 768px){
#errorshooting .error-list ul li{width:49.15254%;float:left
}
#errorshooting .error-list ul li:nth-child(2n+1){margin-left:0;margin-right:-100%;clear:both;margin-left:0
}
#errorshooting .error-list ul li:nth-child(2n+2){margin-left:50.84746%;margin-right:-100%;clear:none
}
}
@media (min-width: 1280px){
#errorshooting .error-list ul li{width:21.73913%;float:left
}
#errorshooting .error-list ul li:nth-child(4n+1){margin-left:0;margin-right:-100%;clear:both;margin-left:0
}
#errorshooting .error-list ul li:nth-child(4n+2){margin-left:26.08696%;margin-right:-100%;clear:none
}
#errorshooting .error-list ul li:nth-child(4n+3){margin-left:52.17391%;margin-right:-100%;clear:none
}
#errorshooting .error-list ul li:nth-child(4n+4){margin-left:78.26087%;margin-right:-100%;clear:none
}
}
@media (min-width: 1680px){
#errorshooting .error-list ul li{width:20.58824%;float:left
}
