}
#errorshooting .error-list ul li:nth-child(4n+2){margin-left:26.47059%;margin-right:-100%;clear:none
}
#errorshooting .error-list ul li:nth-child(4n+3){margin-left:52.94118%;margin-right:-100%;clear:none
}
#errorshooting .error-list ul li:nth-child(4n+4){margin-left:79.41176%;margin-right:-100%;clear:none
}
}
#errorshooting .error-list ul img{width:100%
}
#errorshooting .error-list .title{position:relative;top:-3.5em
}
