#errorshooting .error-list .title{left:-4em;width:120%
}
}
@media (min-width: 1680px){
#errorshooting .error-list .title{left:-6em;width:130%
}
}
#errorshooting .error-list .title a{position:relative;box-shadow:3px 3px 6px rgba(0,0,0,0.66)
