
.transition{transition:.3s all ease-in-out
}
.clr:before,#news-single-slider:before,.clr:after,#news-single-slider:after{content:'';clear:both;display:block
}
#news-single{background-color:#f7f7f7
}
#news-single header{color:#fff;background-size:cover;background-repeat:no-repeat;background-position:center;position:relative;padding:2em 0 4em 0
}
#news-single header:after{content:'';display:block;position:absolute;right:0;left:0;bottom:0;height:200%;background:radial-gradient(ellipse at center, rgba(0,0,0,0.29) 0%, #000 81%, #000 99%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr='#4a000000', endColorstr='#000000',GradientType=1 )
}
#news-single header h1{font-size:3.6em;line-height:1
}
#news-single header h1 span{display:block
}
#news-single header h2{margin:0
}
#news-single header hr{height:1px;border:none;background-color:#76b52a
}
#news-single header .container{position:relative;z-index:1
}
#news-single header .article-title{margin-top:8em
}
.post{background-color:#f7f7f7;background-image:url("../assets/images/components/news-bg-2.png");background-repeat:no-repeat;background-position:top right
}
.post>.container{padding:4rem 0;position:relative;margin-top:-6rem
}
.post .content{padding:0 0 4em 0
}
.news-navigation .fa{position:absolute;top:0
}
.news-navigation a{margin:.5em;position:relative
}
.news-navigation a:first-child{padding-left:4em
}
.news-navigation a:first-child .fa{left:.5em
}
.news-navigation a:last-child{padding-right:4em
}
.news-navigation a:last-child .fa{right:.5em
}
.news-navigation span{display:inline-block;vertical-align:middle;margin:0 .5em
}
#news-single-slider{text-align:center
}
#news-single-slider .slick-prev,#news-single-slider .slick-next{z-index:7;height:40px;width:20px;background-color:#76b52a;cursor:pointer;opacity:.75
}
#news-single-slider .slick-prev:before,#news-single-slider .slick-next:before{font-size:16px
}
#news-single-slider .slick-prev:hover,#news-single-slider .slick-next:hover{opacity:1
}
#news-single-slider .slick-prev{left:0
}
#news-single-slider .slick-prev:before{content:'<'
}
#news-single-slider .slick-next{right:0
}
#news-single-slider .slick-next:before{content:'>'
}
#news-single-slider .item{display:inline-block;vertical-align:middle;width:48%;margin:0 1%
}
#news-single-slider img{box-shadow:0 3px 6px rgba(0,0,0,0.66)
}
#news-single-slider .slick-slide{transition:.3s transform ease;margin:0 2px;background-color:#fff
}
#news-single-slider .slick-slide img{width:100%;opacity:.4
}
@media (min-width: 1024px){
#news-single-slider .slick-slide{margin:0 10px
}
}
#news-single-slider .slick-list{padding-top:50px !important;padding-bottom:50px !important;overflow:initial
}
#news-single-slider .slick-current{position:relative;z-index:1;transform:scale(1.2)
}
#news-single-slider .slick-current img{opacity:1
}
@media (min-width: 1024px){
#news-single-slider .slick-current{transform:scale(1.7)
}
}



