
.transition{transition:.3s all ease-in-out
}
.clr:before,.contact-form .check-item:before,.clr:after,.contact-form .check-item:after{content:'';clear:both;display:block
}
#contact{background-color:#76b52a;color:#fff;padding:2em 0;box-sizing:border-box;background-image:url("../assets/images/components/contact-bg.png");background-repeat:no-repeat;background-size:cover;background-position:left bottom;transition:.3s all ease
}
@media (min-width: 1280px){
#contact{min-height:calc( 100vh - 130px);background-size:auto 90%;background-position:5em top
}
}
@media (min-width: 1680px){
#contact{background-position:right top
}
}
#contact .title h1{font-size:3.6em;line-height:1
}
#contact .title h1 span{display:block
}
#contact .title hr{height:1px;border:none;background-color:#fff
}
.contact-wrapper .address{border-top:1px solid #fff;padding:2em 0 0 0
}
.contact-wrapper .address span{margin-left:.5em
}
.contact-wrapper .email{margin:2em 0
}
.contact-wrapper .email a{color:#fff
}
@media (min-width: 1280px){
.contact-wrapper .block{width:30.43478%;float:left;margin-right:4.34783%
}
.contact-wrapper .block:last-child{width:65.21739%;float:right;margin-right:0
}
}
.contact-form{margin-top:2em;background-color:rgba(0,0,0,0.2);padding:2em;box-sizing:border-box;font-size:1.3em;border:1px solid #fff
}
@media (min-width: 1280px){
.contact-form{margin-top:0
}
}
.contact-form textarea{background-color:transparent;border-top:0;border-right:0;border-left:0;border-bottom:1px solid #fff;color:#fff
}
.contact-form textarea:focus{background-color:rgba(118,181,42,0.3)
}
.contact-form input[type="text"],.contact-form input[type="number"],.contact-form input[type="email"],.contact-form input[type="password"] input[type="search"]{background-color:transparent;border-top:0;border-right:0;border-left:0;border-bottom:1px solid #fff;color:#fff
}
.contact-form input[type="text"]:focus,.contact-form input[type="number"]:focus,.contact-form input[type="email"]:focus,.contact-form input[type="password"] input[type="search"]:focus{background-color:rgba(118,181,42,0.3)
}
.contact-form input[type="checkbox"]{display:inline-block;vertical-align:middle;width:auto;margin-right:.5em;margin-top:.35em;float:left
}
.contact-form input[type="checkbox"]+label{display:block;float:none;margin-left:30px;line-height:1.6;width:auto;vertical-align:middle
}
.contact-form .privacy-checkbox{font-size:.7em
}
.contact-form ::-webkit-input-placeholder{color:#fff
}
.contact-form :-moz-placeholder{color:#fff;opacity:1
}
.contact-form ::-moz-placeholder{color:#fff;opacity:1
}
.contact-form :-ms-input-placeholder{color:#fff
}



