
.transition{transition:.3s all ease-in-out
}
.clr:before,#news-slider .item:before,.clr:after,#news-slider .item:after{content:'';clear:both;display:block
}
#news-slider{color:#fff;background-color:#161616;background-image:url("../assets/images/components/news-bg-1.png");background-repeat:no-repeat;background-position:center top
}
#news-slider h1{font-size:3.6em;line-height:1
}
#news-slider h1 span{display:block
}
#news-slider .item .block{position:relative
}
#news-slider .item .block:first-child{margin-bottom:3em
}
#news-slider .item .block:first-child:after{content:'';border:1px solid #76b52a;display:block;width:100%;height:100%;position:absolute;top:1em;left:1em
}
#news-slider .item .block img{width:100%;position:relative;z-index:1
}
#news-slider .item h2{margin:0
}
#news-slider .item time{color:#76b52a
}
@media (min-width: 768px){
#news-slider .item .block{width:47.05882%;float:left;margin-right:5.88235%
}
#news-slider .item .block:last-child{float:right;margin-right:0
}
}
#news-slider #slider{margin:2em 0;height:100%
}
#news-slider #slider .item{height:100%;position:relative
}
#news-slider #slider .slick-list,#news-slider #slider .slick-track{height:100%
}
#news-slider #slider .slick-dots li{width:40px;height:6px
}
#news-slider #slider .slick-dots li button{width:40px;height:6px;padding:0
}
#news-slider #slider .slick-dots li button:before{content:'';width:40px;height:6px;color:#76b52a;border:1px solid #76b52a;background-color:transparent;opacity:1
}
#news-slider #slider .slick-dots li button:hover:before{background-color:#76b52a
}
#news-slider #slider .slick-dots li.slick-active button:before{background-color:#76b52a
}
#news-list{background-color:#f7f7f7;background-image:url("../assets/images/components/news-bg-2.png");background-repeat:no-repeat;background-position:top right;padding:2em 0
}
#news-list .pagination{margin:2em 0 0 0
}
#news-list .pagination a{display:inline-block;vertical-align:middle;color:#474747;font-size:1.1em;border-right:1px solid #474747;padding:0 2em;box-sizing:border-box
}
#news-list .pagination a.active,#news-list .pagination a:hover{color:#76b52a
}
#news-list .pagination a:first-child,#news-list .pagination a:last-child,#news-list .pagination a:nth-last-child(2){border:0
}
#discover{text-align:center;padding:4em 0;background-color:#76b52a;background-image:url("../assets/images/components/footer-green-bg.png");background-size:100% 100%;background-repeat:no-repeat
}
#discover a{display:inline-block;vertical-align:middle;color:#000;position:relative
}
#discover a:after{content:'';display:block;border-bottom:2px solid #fff;position:absolute;bottom:-1em;left:0;right:0;width:20%;margin:auto
}



