
.transition{transition:.3s all ease-in-out
}
.clr:before,.news-list:before,.clr:after,.news-list:after{content:'';clear:both;display:block
}
.track-animate{transform:translateY(60px);opacity:0;transition:.5s all ease-in
}
.track-animate.os{opacity:1;transform:translateY(0px)
}
#intro{height:40vh
}
#scene{height:100%;margin:0;padding:0;overflow:hidden
}
#scene li{position:absolute;width:100%;height:100%
}
#scene .background{position:absolute;top:0;bottom:0;right:0;left:0;background-size:cover;background-position:center bottom;background-image:url("../assets/images/parallax/background.jpg")
}
#scene .people{position:absolute;bottom:-2%;right:5%;width:70%
}
#intro-brand{background-color:#474747
}
#intro-brand .block{background-size:cover;background-position:center;position:relative;color:#fff
}
#intro-brand .block:first-child{background-image:url("../assets/images/components/intro-brand-1.png")
}
#intro-brand .block .overlay{position:absolute;bottom:0;left:0;right:0;color:#fff;padding:0 2em;margin:auto;text-align:center
}
#intro-brand .block .overlay h1{display:inline-block;max-width:100%;text-align:left
}
#intro-brand .title{background-color:#76b52a;margin-top:2em
}
#intro-brand .title .restrict-small{padding-bottom:0
}
#intro-brand .title h1{margin:0;padding-top:3em;line-height:1
}
#intro-product{background-image:url("../assets/images/components/intro-product-background.png");background-position:right bottom;background-size:cover;background-color:#fff
}
#intro-product h1{margin-bottom:0.2em
}
#intro-product h1 small{display:block;color:#fff
}
#intro-product .product-image{max-width:460px;margin:auto
}
#intro-product .call-action .btn.btn-with-icon{margin-bottom:.5rem
}
#intro-news{background-color:#000;background-image:url("../assets/images/components/intro-news-background.png");background-position:center;background-size:cover;color:#fff;position:relative
}
#intro-news .cover{position:absolute;top:0;right:0;left:0;bottom:0;background-image:url("../assets/images/components/intro-news-cover.png");background-position:center;background-size:100%;background-repeat:no-repeat
}
#intro-news .container{position:relative
}
#intro-news h1{display:inline-block;vertical-align:middle
}
#intro-news h1 small{display:block;font-size:.3em
}
#intro-news h1 small a{color:#76b52a;display:block
}
.news-list .news-item{border:1px solid #76b52a;margin-bottom:1em
}
.news-list .news-item a{display:block;color:#fff
}
.news-list .news-item h4{margin:0;height:3em;overflow:hidden
}
.news-list .news-item time{font-size:.9em
}
.news-list .news-item .thumbnail{position:relative;overflow:hidden
}
.news-list .news-item .thumbnail:hover img{transition:3s all ease-out;transform:scale(1.6)
}
.news-list .news-item .thumbnail img{width:100%
}
.news-list .news-item .thumbnail .overlay{position:absolute;top:0;bottom:0;right:0;left:0;text-align:center;background-color:rgba(0,0,0,0.8)
}
.news-list .news-item .thumbnail .overlay:before{content:'';display:inline-block;vertical-align:middle;width:0;height:100%
}
.news-list .news-item .thumbnail .overlay .title{display:inline-block;vertical-align:middle;width:90%
}
.news-list .news-item .content{padding:1em 2em;height:4em;margin-bottom:1em;overflow:hidden;text-align:left;font-size:.9em
}
.news-list .news-item .footer{padding:.5em 1em;background-color:#76b52a
}
@media (min-width: 768px){
.news-list .news-item{width:49.15254%;float:left
}
.news-list .news-item:nth-child(2n+1){margin-left:0;margin-right:-100%;clear:both;margin-left:0
}
.news-list .news-item:nth-child(2n+2){margin-left:50.84746%;margin-right:-100%;clear:none
}
#footer .footer-inner .block{width:32.20339%;float:left;margin-right:1.69492%;margin:0;text-align:left
}
#footer .footer-inner .block:last-child{width:66.10169%;float:left;margin-right:1.69492%;text-align:right;float:right;margin-right:0
}
}
@media (min-width: 1024px){
#intro{height:70vh
}
#scene .people{position:absolute;bottom:-5%
}
}
@media (min-width: 1280px){
#intro{height:calc( 100vh - 80px)
}
#intro-brand{height:100vh
}
#intro-brand .table{height:100%
}
#intro-brand .table img{display:none
}
#intro-brand .title h1{padding-top:2.5em;font-size:4em
}
#intro-product{box-sizing:border-box;padding:2em 0
}
#intro-news{height:100vh
}
.news-list .news-item{width:22.85714%;float:left
}
.news-list .news-item:nth-child(4n+1){margin-left:0;margin-right:-100%;clear:both;margin-left:0
}
.news-list .news-item:nth-child(4n+2){margin-left:25.71429%;margin-right:-100%;clear:none
}
.news-list .news-item:nth-child(4n+3){margin-left:51.42857%;margin-right:-100%;clear:none
}
.news-list .news-item:nth-child(4n+4){margin-left:77.14286%;margin-right:-100%;clear:none
}
}



