
.transition{transition:.3s all ease-in-out
}
.clr:before,.inventory-list:before,.inventory-list li:before,.clr:after,.inventory-list:after,.inventory-list li:after{content:'';clear:both;display:block
}
#inventory{position:fixed;top:0;bottom:0;right:0;left:0;z-index:999;background-color:rgba(0,0,0,0.9);color:#fff
}
.inventory-wrapper{height:100%
}
.inventory-wrapper .title h1{font-size:3.6em;line-height:1
}
.inventory-wrapper .title h1 span{display:block
}
.inventory-list{margin:0;padding:0;list-style-type:none
}
.inventory-list img{width:100%
}
.inventory-list .thumbnail{margin-bottom:1em;border:1px solid #fff;box-sizing:border-box
}
.inventory-list .name{font-size:1.6em;font-family:"GothamPro-Bold","Helvetica Neue",Arial,"source-han-sans-traditional",sans-serif;margin-bottom:.5em
}
.inventory-list .call-action a{margin:0
}
.inventory-list li{margin-bottom:1em;border-bottom:1px dotted #fff;padding:1em 0
}
@media (min-width: 768px){
.inventory-list li .thumbnail{width:21.73913%;float:left;margin-right:4.34783%
}
.inventory-list li .content{width:73.91304%;float:right;margin-right:0
}
}
.leave-inventory{position:absolute;top:1em;right:1em;color:#76b52a;font-size:2em
}



