
.transition{transition:.3s all ease-in-out
}
.clr:before,.clr:after{content:'';clear:both;display:block
}
#subscription{background-color:rgba(71,71,71,0.9);position:fixed;top:0;right:0;left:0;z-index:9999;box-shadow:0 0 6px rgba(0,0,0,0.66)
}
#subscription h3{color:#fff
}
.subscription-wrapper{position:relative
}
.subscription-wrapper #close{display:block;position:absolute;width:40px;height:40px;top:0;right:0;padding:0;text-align:center;line-height:40px
}



